import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { saveToStorage, getFromStorage } from '../../utils/storage';

// 
import TodoItem from './components/TodoItem';
import TodoModal from './components/TodoModal';

// 
import AddIcon from '@mui/icons-material/Add';

const TodoContainer = styled.div`
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
`;

const TodoHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
`;

const TodoTitle = styled.h2`
  margin: 0;
  font-size: 24px;
  font-weight: 500;
`;

const ActionButton = styled.button`
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  
  &:hover {
    background-color: #3a7bc8;
  }
`;

const TodoList = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const EmptyState = styled.div`
  text-align: center;
  padding: 40px 20px;
  color: var(--text-color);
  opacity: 0.7;
`;

// 
const notificationSound = new Audio('data:audio/wav;base64,');

const Todo = () => {
  // 
  const [todos, setTodos] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [modalMode, setModalMode] = useState('add'); // add, edit
  const [editingItem, setEditingItem] = useState(null);
  
  // 
  useEffect(() => {
    const savedTodos = getFromStorage('homepage-todos', []);
    setTodos(savedTodos);
    
    // 
    const checkNotifications = () => {
      const now = new Date();
      
      savedTodos.forEach(todo => {
        if (todo.notification && !todo.completed) {
          const notificationTime = new Date(todo.notification);
          
          // 1
          if (Math.abs(now - notificationTime) < 60000 && !todo.notified) {
            // 
            if (Notification.permission === 'granted') {
              new Notification('', {
                body: todo.name,
                icon: '/icons/icon128.png'
              });
              
              // 
              notificationSound.play();
              
              // 
              const updatedTodos = savedTodos.map(item => {
                if (item.id === todo.id) {
                  return { ...item, notified: true };
                }
                return item;
              });
              
              setTodos(updatedTodos);
              saveToStorage('homepage-todos', updatedTodos);
            }
          }
        }
      });
    };
    
    // 
    const notificationInterval = setInterval(checkNotifications, 60000);
    
    // 
    if (Notification.permission !== 'granted' && Notification.permission !== 'denied') {
      Notification.requestPermission();
    }
    
    return () => {
      clearInterval(notificationInterval);
    };
  }, []);
  
  // 
  const openAddTodoModal = () => {
    setModalMode('add');
    setEditingItem(null);
    setShowModal(true);
  };
  
  // 
  const openEditTodoModal = (todo) => {
    setModalMode('edit');
    setEditingItem(todo);
    setShowModal(true);
  };
  
  // 
  const closeModal = () => {
    setShowModal(false);
  };
  
  // 
  const saveTodo = (formData) => {
    const newTodo = {
      id: editingItem ? editingItem.id : Date.now(),
      name: formData.name,
      date: formData.date,
      notification: formData.notification,
      completed: formData.completed || false,
      notified: false
    };
    
    let updatedTodos;
    
    if (modalMode === 'edit') {
      // 
      updatedTodos = todos.map(item => 
        item.id === editingItem.id ? newTodo : item
      );
    } else {
      // 
      updatedTodos = [...todos, newTodo];
    }
    
    setTodos(updatedTodos);
    saveToStorage('homepage-todos', updatedTodos);
    closeModal();
  };
  
  // 
  const deleteTodo = (id) => {
    if (window.confirm('')) {
      const updatedTodos = todos.filter(item => item.id !== id);
      setTodos(updatedTodos);
      saveToStorage('homepage-todos', updatedTodos);
    }
  };
  
  // 
  const toggleTodoComplete = (id) => {
    const updatedTodos = todos.map(todo => {
      if (todo.id === id) {
        return { ...todo, completed: !todo.completed };
      }
      return todo;
    });
    
    setTodos(updatedTodos);
    saveToStorage('homepage-todos', updatedTodos);
  };
  
  // 
  const sortedTodos = [...todos].sort((a, b) => {
    // 
    if (a.completed !== b.completed) {
      return a.completed ? 1 : -1;
    }
    
    // 
    return new Date(a.date) - new Date(b.date);
  });
  
  return (
    <TodoContainer>
      <TodoHeader>
        <TodoTitle></TodoTitle>
        <ActionButton onClick={openAddTodoModal} title="">
          <AddIcon />
        </ActionButton>
      </TodoHeader>
      
      <TodoList>
        {sortedTodos.map(todo => (
          <TodoItem 
            key={todo.id}
            todo={todo}
            onToggleComplete={toggleTodoComplete}
            onEdit={openEditTodoModal}
            onDelete={deleteTodo}
          />
        ))}
        
        {todos.length === 0 && (
          <EmptyState>
            <p></p>
          </EmptyState>
        )}
      </TodoList>
      
      <TodoModal 
        isOpen={showModal}
        onClose={closeModal}
        onSave={saveTodo}
        todo={editingItem}
      />
    </TodoContainer>
  );
};

export default Todo;
