import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { saveToStorage, getFromStorage } from '../../utils/storage';

// 
import SettingsIcon from '@mui/icons-material/Settings';
import TextIncreaseIcon from '@mui/icons-material/TextIncrease';
import TextDecreaseIcon from '@mui/icons-material/TextDecrease';

const QuoteContainer = styled.div`
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
`;

const QuoteContent = styled.div`
  text-align: center;
  padding: 30px 20px;
  font-size: ${props => props.fontSize}px;
  line-height: 1.5;
  font-weight: 500;
  color: var(--text-color);
  transition: font-size 0.3s ease;
`;

const QuoteControls = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin-top: 20px;
`;

const ControlButton = styled.button`
  background-color: transparent;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: var(--text-secondary-color);
  
  &:hover {
    background-color: rgba(0, 0, 0, 0.05);
    color: var(--primary-color);
  }
`;

const Modal = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
`;

const ModalContent = styled.div`
  background-color: var(--background-color);
  padding: 20px;
  border-radius: 8px;
  width: 90%;
  max-width: 600px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
`;

const ModalHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
`;

const ModalTitle = styled.h3`
  margin: 0;
  font-size: 18px;
  font-weight: 500;
`;

const CloseButton = styled.button`
  background: none;
  border: none;
  cursor: pointer;
  font-size: 20px;
  color: var(--text-color);
`;

const TextArea = styled.textarea`
  width: 100%;
  height: 300px;
  padding: 10px;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  background-color: var(--background-color);
  color: var(--text-color);
  font-size: 14px;
  resize: vertical;
  font-family: inherit;
`;

const ButtonGroup = styled.div`
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 20px;
`;

const Button = styled.button`
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  
  &.primary {
    background-color: var(--primary-color);
    color: white;
    
    &:hover {
      background-color: #3a7bc8;
    }
  }
  
  &.secondary {
    background-color: var(--card-background);
    color: var(--text-color);
    border: 1px solid var(--border-color);
    
    &:hover {
      background-color: rgba(0, 0, 0, 0.05);
    }
  }
`;

const Quote = () => {
  // 
  const [quotes, setQuotes] = useState(['']);
  const [currentQuote, setCurrentQuote] = useState('');
  const [fontSize, setFontSize] = useState(24);
  const [showModal, setShowModal] = useState(false);
  const [editText, setEditText] = useState('');
  
  // 
  useEffect(() => {
    const savedQuotes = getFromStorage('homepage-quotes', ['']);
    const savedFontSize = getFromStorage('homepage-quote-font-size', 24);
    
    setQuotes(savedQuotes);
    setFontSize(savedFontSize);
    
    // 
    if (savedQuotes.length > 0) {
      const randomIndex = Math.floor(Math.random() * savedQuotes.length);
      setCurrentQuote(savedQuotes[randomIndex]);
    }
  }, []);
  
  // 
  const increaseFontSize = () => {
    const newSize = Math.min(fontSize + 2, 48);
    setFontSize(newSize);
    saveToStorage('homepage-quote-font-size', newSize);
  };
  
  // 
  const decreaseFontSize = () => {
    const newSize = Math.max(fontSize - 2, 16);
    setFontSize(newSize);
    saveToStorage('homepage-quote-font-size', newSize);
  };
  
  // 
  const openConfigModal = () => {
    setEditText(quotes.join('\n'));
    setShowModal(true);
  };
  
  // 
  const closeModal = () => {
    setShowModal(false);
  };
  
  // 
  const handleTextChange = (e) => {
    setEditText(e.target.value);
  };
  
  // 
  const saveConfig = () => {
    // 
    const newQuotes = editText
      .split('\n')
      .map(line => line.trim())
      .filter(line => line.length > 0);
    
    // 
    if (newQuotes.length === 0) {
      newQuotes.push('');
    }
    
    setQuotes(newQuotes);
    saveToStorage('homepage-quotes', newQuotes);
    
    // 
    const randomIndex = Math.floor(Math.random() * newQuotes.length);
    setCurrentQuote(newQuotes[randomIndex]);
    
    closeModal();
  };
  
  return (
    <QuoteContainer>
      <QuoteContent fontSize={fontSize}>
        "{currentQuote}"
      </QuoteContent>
      
      <QuoteControls>
        <ControlButton onClick={decreaseFontSize} title="">
          <TextDecreaseIcon />
        </ControlButton>
        <ControlButton onClick={openConfigModal} title="">
          <SettingsIcon />
        </ControlButton>
        <ControlButton onClick={increaseFontSize} title="">
          <TextIncreaseIcon />
        </ControlButton>
      </QuoteControls>
      
      {showModal && (
        <Modal>
          <ModalContent>
            <ModalHeader>
              <ModalTitle></ModalTitle>
              <CloseButton onClick={closeModal}>&times;</CloseButton>
            </ModalHeader>
            
            <TextArea 
              value={editText} 
              onChange={handleTextChange}
              placeholder=""
            />
            
            <ButtonGroup>
              <Button className="secondary" onClick={closeModal}></Button>
              <Button className="primary" onClick={saveConfig}></Button>
            </ButtonGroup>
          </ModalContent>
        </Modal>
      )}
    </QuoteContainer>
  );
};

export default Quote;