import { format, addDays, differenceInDays, isValid, parse } from 'date-fns';
import { zhCN } from 'date-fns/locale';

/**
 * 
 * @param {Date|number} date - 
 * @param {string} formatStr - 
 * @param {Object} options - 
 * @returns {string} 
 */
export const formatDateTime = (date, formatStr = 'yyyy-MM-dd HH:mm:ss', options = {}) => {
  try {
    const dateObj = date instanceof Date ? date : new Date(date);
    return format(dateObj, formatStr, {
      locale: zhCN,
      ...options
    });
  } catch (error) {
    console.error(':', error);
    return '';
  }
};

/**
 * 
 * @param {string} formatStr - 
 * @returns {string} 
 */
export const getCurrentDateTime = (formatStr = 'yyyy-MM-dd HH:mm:ss') => {
  return formatDateTime(new Date(), formatStr);
};

/**
 * 
 * @param {Date|number|string} startDate - 
 * @param {Date|number|string} endDate - 
 * @returns {number} 
 */
export const getDaysDifference = (startDate, endDate) => {
  try {
    const start = startDate instanceof Date ? startDate : new Date(startDate);
    const end = endDate instanceof Date ? endDate : new Date(endDate);
    return differenceInDays(end, start);
  } catch (error) {
    console.error(':', error);
    return 0;
  }
};

/**
 * 
 * @param {Date|number|string} date - 
 * @param {number} days - 
 * @returns {Date} 
 */
export const addDaysToDate = (date, days) => {
  try {
    const dateObj = date instanceof Date ? date : new Date(date);
    return addDays(dateObj, days);
  } catch (error) {
    console.error(':', error);
    return new Date();
  }
};

/**
 * 
 * @param {string} dateStr - 
 * @param {string} formatStr - 
 * @returns {Date|null} null
 */
export const parseDate = (dateStr, formatStr = 'yyyy-MM-dd') => {
  try {
    const parsedDate = parse(dateStr, formatStr, new Date());
    return isValid(parsedDate) ? parsedDate : null;
  } catch (error) {
    console.error(':', error);
    return null;
  }
};

