import React, { useState, useContext } from 'react';
import styled from 'styled-components';
import { buildSearchUrl, cleanUrl } from '../../utils/urlHandler';
import { SettingsContext } from '../../contexts/SettingsContext';

// 
import SearchIcon from '@mui/icons-material/Search';
import CleaningServicesIcon from '@mui/icons-material/CleaningServices';

const SearchContainer = styled.div`
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  padding: 10px;
`;

const SearchForm = styled.form`
  display: flex;
  align-items: center;
  background-color: var(--card-background);
  border-radius: 24px;
  padding: 5px 15px;
  box-shadow: 0 2px 5px var(--shadow-color);
  transition: all var(--transition-speed);
  
  &:focus-within {
    box-shadow: 0 4px 10px var(--shadow-color);
    transform: translateY(-2px);
  }
`;

const SearchInput = styled.input`
  flex: 1;
  border: none;
  background: transparent;
  padding: 10px;
  font-size: 16px;
  color: var(--text-color);
  outline: none;
`;

const SearchButton = styled.button`
  background: none;
  border: none;
  color: var(--text-secondary-color);
  cursor: pointer;
  padding: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: color var(--transition-speed);
  
  &:hover {
    color: var(--primary-color);
  }
`;

const CleanButton = styled(SearchButton)`
  margin-right: 5px;
  color: ${props => props.active ? 'var(--primary-color)' : 'var(--text-secondary-color)'};
`;

const EngineSelector = styled.div`
  display: flex;
  justify-content: center;
  margin-top: 10px;
  gap: 10px;
`;

const EngineOption = styled.button`
  background: none;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  font-size: 14px;
  color: ${props => props.active ? 'var(--primary-color)' : 'var(--text-secondary-color)'};
  font-weight: ${props => props.active ? '500' : 'normal'};
  border-bottom: ${props => props.active ? '2px solid var(--primary-color)' : 'none'};
  transition: all var(--transition-speed);
  
  &:hover {
    color: var(--primary-color);
  }
`;

const Search = () => {
  const { settings, updateSpecificSettings } = useContext(SettingsContext);
  const [query, setQuery] = useState('');
  const [autoClean, setAutoClean] = useState(settings.search.autoCleanUrl);
  const [engine, setEngine] = useState(settings.search.defaultEngine);
  
  // 
  const handleSubmit = (e) => {
    e.preventDefault();
    
    if (!query.trim()) return;
    
    const searchUrl = buildSearchUrl(query, engine, autoClean);
    
    if (settings.search.openInNewTab) {
      window.open(searchUrl, '_blank');
    } else {
      window.location.href = searchUrl;
    }
  };
  
  // 
  const handleInputChange = (e) => {
    setQuery(e.target.value);
  };
  
  // URL
  const toggleAutoClean = () => {
    const newValue = !autoClean;
    setAutoClean(newValue);
    updateSpecificSettings('search.autoCleanUrl', newValue);
  };
  
  // 
  const changeEngine = (newEngine) => {
    setEngine(newEngine);
    updateSpecificSettings('search.defaultEngine', newEngine);
  };
  
  return (
    <SearchContainer>
      <SearchForm onSubmit={handleSubmit}>
        <SearchInput
          type="text"
          placeholder=""
          value={query}
          onChange={handleInputChange}
          autoFocus
        />
        <CleanButton
          type="button"
          onClick={toggleAutoClean}
          active={autoClean}
          title={autoClean ? 'URL' : 'URL'}
        >
          <CleaningServicesIcon fontSize="small" />
        </CleanButton>
        <SearchButton type="submit" title="">
          <SearchIcon />
        </SearchButton>
      </SearchForm>
      
      <EngineSelector>
        <EngineOption
          onClick={() => changeEngine('bing')}
          active={engine === 'bing'}
        >
          
        </EngineOption>
        <EngineOption
          onClick={() => changeEngine('google')}
          active={engine === 'google'}
        >
          
        </EngineOption>
        <EngineOption
          onClick={() => changeEngine('baidu')}
          active={engine === 'baidu'}
        >
          
        </EngineOption>
        <EngineOption
          onClick={() => changeEngine('sogou')}
          active={engine === 'sogou'}
        >
          
        </EngineOption>
        <EngineOption
          onClick={() => changeEngine('mita')}
          active={engine === 'mita'}
        >
          AI
        </EngineOption>
      </EngineSelector>
    </SearchContainer>
  );
};

export default Search;