/**
 * 
 * @param {string} key - 
 * @param {any} defaultValue - 
 * @returns {any} 
 */
export const getFromStorage = (key, defaultValue = null) => {
  try {
    const item = localStorage.getItem(key);
    return item ? JSON.parse(item) : defaultValue;
  } catch (error) {
    console.error(` ${key} :`, error);
    return defaultValue;
  }
};

/**
 * 
 * @param {string} key - 
 * @param {any} value - 
 * @returns {boolean} 
 */
export const saveToStorage = (key, value) => {
  try {
    localStorage.setItem(key, JSON.stringify(value));
    return true;
  } catch (error) {
    console.error(` ${key} :`, error);
    return false;
  }
};

/**
 * 
 * @param {string} key - 
 * @returns {boolean} 
 */
export const removeFromStorage = (key) => {
  try {
    localStorage.removeItem(key);
    return true;
  } catch (error) {
    console.error(` ${key} :`, error);
    return false;
  }
};

/**
 * 
 * @returns {boolean} 
 */
export const clearStorage = () => {
  try {
    localStorage.clear();
    return true;
  } catch (error) {
    console.error(':', error);
    return false;
  }
};

/**
 * 
 * @returns {string[]} 
 */
export const getAllStorageKeys = () => {
  try {
    return Object.keys(localStorage);
  } catch (error) {
    console.error(':', error);
    return [];
  }
};

/**
 * JSON
 * @param {string} filename - 
 */
export const exportStorageToFile = (filename = 'homepage-backup.json') => {
  try {
    const data = {};
    const keys = getAllStorageKeys();
    
    keys.forEach(key => {
      if (key.startsWith('homepage-')) {
        data[key] = getFromStorage(key);
      }
    });
    
    const dataStr = JSON.stringify(data, null, 2);
    const dataUri = 'data:application/json;charset=utf-8,'+ encodeURIComponent(dataStr);
    
    const linkElement = document.createElement('a');
    linkElement.setAttribute('href', dataUri);
    linkElement.setAttribute('download', filename);
    linkElement.click();
    
    return true;
  } catch (error) {
    console.error(':', error);
    return false;
  }
};

/**
 * JSON
 * @param {string} jsonString - JSON
 * @returns {boolean} 
 */
export const importStorageFromJson = (jsonString) => {
  try {
    const data = JSON.parse(jsonString);
    
    Object.keys(data).forEach(key => {
      if (key.startsWith('homepage-')) {
        saveToStorage(key, data[key]);
      }
    });
    
    return true;
  } catch (error) {
    console.error(':', error);
    return false;
  }
};