import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { saveToStorage, getFromStorage } from '../../utils/storage';
import axios from 'axios';
import xml2js from 'xml2js';

// 
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import RssFeedIcon from '@mui/icons-material/RssFeed';
import LinkIcon from '@mui/icons-material/Link';

const NewsContainer = styled.div`
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
`;

const NewsHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  background-color: var(--card-background);
  box-shadow: 0 2px 5px var(--shadow-color);
`;

const HeaderTitle = styled.h2`
  margin: 0;
  font-size: 20px;
  font-weight: 500;
`;

const BackButton = styled.button`
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin-right: 15px;
  
  &:hover {
    background-color: #3a7bc8;
  }
`;

const SourcesContainer = styled.div`
  display: flex;
  align-items: center;
  overflow-x: auto;
  padding: 10px 20px;
  background-color: var(--background-color);
  border-bottom: 1px solid var(--border-color);
  
  &::-webkit-scrollbar {
    height: 4px;
  }
  
  &::-webkit-scrollbar-track {
    background: var(--background-color);
  }
  
  &::-webkit-scrollbar-thumb {
    background-color: var(--border-color);
    border-radius: 4px;
  }
`;

const SourceButton = styled.button`
  background-color: ${props => props.active ? 'var(--primary-color)' : 'var(--card-background)'};
  color: ${props => props.active ? 'white' : 'var(--text-color)'};
  border: none;
  border-radius: 20px;
  padding: 8px 15px;
  margin-right: 10px;
  cursor: pointer;
  white-space: nowrap;
  display: flex;
  align-items: center;
  gap: 5px;
  
  &:hover {
    background-color: ${props => props.active ? 'var(--primary-color)' : 'rgba(0, 0, 0, 0.05)'};
  }
  
  svg {
    font-size: 16px;
  }
`;

const AddSourceButton = styled.button`
  background-color: var(--card-background);
  color: var(--primary-color);
  border: 1px dashed var(--primary-color);
  border-radius: 20px;
  padding: 8px 15px;
  margin-right: 10px;
  cursor: pointer;
  white-space: nowrap;
  display: flex;
  align-items: center;
  gap: 5px;
  
  &:hover {
    background-color: rgba(0, 0, 0, 0.05);
  }
`;

const NewsContent = styled.div`
  flex: 1;
  padding: 20px;
  overflow-y: auto;
`;

const NewsList = styled.div`
  display: flex;
  flex-direction: column;
  gap: 15px;
`;

const NewsItem = styled.a`
  display: block;
  padding: 15px;
  background-color: var(--card-background);
  border-radius: 8px;
  box-shadow: 0 2px 5px var(--shadow-color);
  cursor: pointer;
  text-decoration: none;
  color: var(--text-color);
  transition: all var(--transition-speed);
  
  &:hover {
    transform: translateY(-3px);
    box-shadow: 0 5px 15px var(--shadow-color);
  }
  
  &:visited {
    opacity: 0.7;
  }
`;

const NewsTitle = styled.h3`
  margin: 0;
  font-size: 16px;
  font-weight: 500;
`;

const NewsDate = styled.div`
  font-size: 12px;
  color: var(--text-secondary-color);
  margin-top: 5px;
`;

const LoadingState = styled.div`
  text-align: center;
  padding: 40px 20px;
  color: var(--text-color);
`;

const ErrorState = styled.div`
  text-align: center;
  padding: 40px 20px;
  color: var(--text-color);
  color: #e53935;
`;

const EmptyState = styled.div`
  text-align: center;
  padding: 40px 20px;
  color: var(--text-color);
  opacity: 0.7;
`;

const Modal = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
`;

const ModalContent = styled.div`
  background-color: var(--background-color);
  padding: 20px;
  border-radius: 8px;
  width: 90%;
  max-width: 500px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
`;

const ModalHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
`;

const ModalTitle = styled.h3`
  margin: 0;
  font-size: 18px;
  font-weight: 500;
`;

const CloseButton = styled.button`
  background: none;
  border: none;
  cursor: pointer;
  font-size: 20px;
  color: var(--text-color);
`;

const FormGroup = styled.div`
  margin-bottom: 15px;
`;

const Label = styled.label`
  display: block;
  margin-bottom: 5px;
  font-size: 14px;
`;

const Input = styled.input`
  width: 100%;
  padding: 8px;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  background-color: var(--background-color);
  color: var(--text-color);
`;

const Select = styled.select`
  width: 100%;
  padding: 8px;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  background-color: var(--background-color);
  color: var(--text-color);
`;

const ButtonGroup = styled.div`
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 20px;
`;

const Button = styled.button`
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  
  &.primary {
    background-color: var(--primary-color);
    color: white;
    
    &:hover {
      background-color: #3a7bc8;
    }
  }
  
  &.secondary {
    background-color: var(--card-background);
    color: var(--text-color);
    border: 1px solid var(--border-color);
    
    &:hover {
      background-color: rgba(0, 0, 0, 0.05);
    }
  }
  
  &.danger {
    background-color: #e53935;
    color: white;
    
    &:hover {
      background-color: #c62828;
    }
  }
`;

const SourcesList = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 20px;
`;

const SourceItem = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  background-color: var(--card-background);
  border-radius: 4px;
  border-left: 3px solid ${props => props.type === 'rss' ? '#ff9800' : '#2196f3'};
`;

const SourceInfo = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
`;

const SourceName = styled.div`
  font-weight: 500;
`;

const SourceUrl = styled.div`
  font-size: 12px;
  color: var(--text-secondary-color);
  word-break: break-all;
`;

const SourceActions = styled.div`
  display: flex;
  gap: 5px;
`;

const ActionIconButton = styled.button`
  background-color: transparent;
  border: none;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: var(--text-secondary-color);
  
  &:hover {
    background-color: rgba(0, 0, 0, 0.05);
    color: var(--primary-color);
  }
  
  &.delete:hover {
    color: #e53935;
  }
`;

// URL
const CORS_PROXY = 'https://cors-anywhere.herokuapp.com/';

const News = () => {
  // 
  const [sources, setSources] = useState([]);
  const [activeSource, setActiveSource] = useState(null);
  const [newsItems, setNewsItems] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const [showSourcesModal, setShowSourcesModal] = useState(false);
  const [editingSource, setEditingSource] = useState(null);
  
  const [formData, setFormData] = useState({
    name: '',
    url: '',
    type: 'rss' // rss  html
  });
  
  // 
  useEffect(() => {
    const savedSources = getFromStorage('homepage-news-sources', []);
    setSources(savedSources);
    
    if (savedSources.length > 0) {
      setActiveSource(savedSources[0]);
    }
  }, []);
  
  // 
  useEffect(() => {
    if (activeSource) {
      loadNews(activeSource);
    }
  }, [activeSource]);
  
  // 
  const loadNews = async (source) => {
    setLoading(true);
    setError(null);
    setNewsItems([]);
    
    try {
      if (source.type === 'rss') {
        await loadRssNews(source);
      } else {
        await loadHtmlNews(source);
      }
    } catch (err) {
      console.error(':', err);
      setError(`: ${err.message}`);
    } finally {
      setLoading(false);
    }
  };
  
  // RSS
  const loadRssNews = async (source) => {
    try {
      // 
      const response = await axios.get(`${CORS_PROXY}${source.url}`);
      const xmlData = response.data;
      
      // XML
      xml2js.parseString(xmlData, (err, result) => {
        if (err) {
          throw new Error('RSS');
        }
        
        let items = [];
        
        // RSS
        if (result.rss && result.rss.channel && result.rss.channel[0].item) {
          // RSS 2.0
          items = result.rss.channel[0].item.map(item => ({
            title: item.title ? item.title[0] : '',
            link: item.link ? item.link[0] : '#',
            date: item.pubDate ? new Date(item.pubDate[0]) : new Date(),
            description: item.description ? item.description[0] : ''
          }));
        } else if (result.feed && result.feed.entry) {
          // Atom
          items = result.feed.entry.map(entry => ({
            title: entry.title ? entry.title[0] : '',
            link: entry.link && entry.link[0].$ ? entry.link[0].$.href : '#',
            date: entry.updated ? new Date(entry.updated[0]) : new Date(),
            description: entry.summary ? entry.summary[0] : ''
          }));
        }
        
        setNewsItems(items);
      });
    } catch (error) {
      console.error('RSS:', error);
      throw new Error('RSS');
    }
  };
  
  // HTML
  const loadHtmlNews = async (source) => {
    try {
      // 
      const response = await axios.get(`${CORS_PROXY}${source.url}`);
      const htmlData = response.data;
      
      // DOMHTML
      const parser = new DOMParser();
      const doc = parser.parseFromString(htmlData, 'text/html');
      
      // 
      // 
      const headings = Array.from(doc.querySelectorAll('h1, h2, h3, .title, .headline, article header'));
      
      const items = headings.map(heading => {
        // 
        let link = '#';
        const linkElement = heading.querySelector('a') || heading.closest('a');
        if (linkElement && linkElement.href) {
          // URL
          if (linkElement.href.startsWith('/')) {
            const url = new URL(source.url);
            link = `${url.protocol}//${url.host}${linkElement.href}`;
          } else {
            link = linkElement.href;
          }
        }
        
        return {
          title: heading.textContent.trim(),
          link,
          date: new Date(),
          description: ''
        };
      }).filter(item => item.title); // 
      
      setNewsItems(items);
    } catch (error) {
      console.error('HTML:', error);
      throw new Error('HTML');
    }
  };
  
  // 
  const openAddSourceModal = () => {
    setEditingSource(null);
    setFormData({
      name: '',
      url: '',
      type: 'rss'
    });
    setShowModal(true);
  };
  
  // 
  const openEditSourceModal = (source) => {
    setEditingSource(source);
    setFormData({
      name: source.name,
      url: source.url,
      type: source.type
    });
    setShowModal(true);
  };
  
  // 
  const closeModal = () => {
    setShowModal(false);
    setEditingSource(null);
  };
  
  // 
  const openSourcesModal = () => {
    setShowSourcesModal(true);
  };
  
  // 
  const closeSourcesModal = () => {
    setShowSourcesModal(false);
  };
  
  // 
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };
  
  // 
  const saveSource = () => {
    if (!formData.name.trim() || !formData.url.trim()) {
      alert('');
      return;
    }
    
    const newSource = {
      id: editingSource ? editingSource.id : Date.now(),
      name: formData.name.trim(),
      url: formData.url.trim(),
      type: formData.type
    };
    
    let updatedSources;
    
    if (editingSource) {
      // 
      updatedSources = sources.map(source => 
        source.id === editingSource.id ? newSource : source
      );
    } else {
      // 
      updatedSources = [...sources, newSource];
    }
    
    setSources(updatedSources);
    saveToStorage('homepage-news-sources', updatedSources);
    
    // 
    if (sources.length === 0) {
      setActiveSource(newSource);
    }
    
    closeModal();
  };
  
  // 
  const deleteSource = (id) => {
    if (window.confirm('')) {
      const updatedSources = sources.filter(source => source.id !== id);
      setSources(updatedSources);
      saveToStorage('homepage-news-sources', updatedSources);
      
      // 
      if (activeSource && activeSource.id === id) {
        if (updatedSources.length > 0) {
          setActiveSource(updatedSources[0]);
        } else {
          setActiveSource(null);
          setNewsItems([]);
        }
      }
      
      // 
      if (!showSourcesModal) {
        closeModal();
      }
    }
  };
  
  // 
  const goBack = () => {
    window.history.back();
  };
  
  return (
    <NewsContainer>
      <NewsHeader>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <BackButton onClick={goBack} title="">
            <ArrowBackIcon />
          </BackButton>
          <HeaderTitle></HeaderTitle>
        </div>
      </NewsHeader>
      
      <SourcesContainer>
        {sources.map(source => (
          <SourceButton 
            key={source.id} 
            active={activeSource && activeSource.id === source.id}
            onClick={() => setActiveSource(source)}
          >
            {source.type === 'rss' ? <RssFeedIcon /> : <LinkIcon />}
            {source.name}
          </SourceButton>
        ))}
        
        <AddSourceButton onClick={openSourcesModal}>
          <AddIcon />
          
        </AddSourceButton>
      </SourcesContainer>
      
      <NewsContent>
        {loading ? (
          <LoadingState>...</LoadingState>
        ) : error ? (
          <ErrorState>{error}</ErrorState>
        ) : newsItems.length > 0 ? (
          <NewsList>
            {newsItems.map((item, index) => (
              <NewsItem key={index} href={item.link} target="_blank" rel="noopener noreferrer">
                <NewsTitle>{item.title}</NewsTitle>
                {item.date && (
                  <NewsDate>
                    {item.date.toLocaleDateString('zh-CN', {
                      year: 'numeric',
                      month: 'long',
                      day: 'numeric'
                    })}
                  </NewsDate>
                )}
              </NewsItem>
            ))}
          </NewsList>
        ) : activeSource ? (
          <EmptyState>
            <p></p>
          </EmptyState>
        ) : (
          <EmptyState>
            <p></p>
          </EmptyState>
        )}
      </NewsContent>
      
      {/* / */}
      {showModal && (
        <Modal>
          <ModalContent>
            <ModalHeader>
              <ModalTitle>{editingSource ? '' : ''}</ModalTitle>
              <CloseButton onClick={closeModal}>&times;</CloseButton>
            </ModalHeader>
            
            <FormGroup>
              <Label></Label>
              <Input 
                type="text" 
                name="name" 
                value={formData.name} 
                onChange={handleInputChange} 
                placeholder=""
              />
            </FormGroup>
            
            <FormGroup>
              <Label></Label>
              <Select 
                name="type" 
                value={formData.type} 
                onChange={handleInputChange}
              >
                <option value="rss">RSS </option>
                <option value="html"></option>
              </Select>
            </FormGroup>
            
            <FormGroup>
              <Label>URL</Label>
              <Input 
                type="text" 
                name="url" 
                value={formData.url} 
                onChange={handleInputChange} 
                placeholder={formData.type === 'rss' ? 'RSS ' : ''}
              />
            </FormGroup>
            
            <ButtonGroup>
              {editingSource && (
                <Button 
                  className="danger" 
                  onClick={() => deleteSource(editingSource.id)}
                >
                  
                </Button>
              )}
              <Button className="secondary" onClick={closeModal}></Button>
              <Button className="primary" onClick={saveSource}></Button>
            </ButtonGroup>
          </ModalContent>
        </Modal>
      )}
      
      {/*  */}
      {showSourcesModal && (
        <Modal>
          <ModalContent>
            <ModalHeader>
              <ModalTitle></ModalTitle>
              <CloseButton onClick={closeSourcesModal}>&times;</CloseButton>
            </ModalHeader>
            
            <Button className="primary" onClick={openAddSourceModal} style={{ width: '100%' }}>
              <AddIcon /> 
            </Button>
            
            <SourcesList>
              {sources.map(source => (
                <SourceItem key={source.id} type={source.type}>
                  <SourceInfo>
                    {source.type === 'rss' ? <RssFeedIcon /> : <LinkIcon />}
                    <div>
                      <SourceName>{source.name}</SourceName>
                      <SourceUrl>{source.url}</SourceUrl>
                    </div>
                  </SourceInfo>
                  <SourceActions>
                    <ActionIconButton onClick={() => openEditSourceModal(source)}>
                      <EditIcon fontSize="small" />
                    </ActionIconButton>
                    <ActionIconButton 
                      className="delete" 
                      onClick={() => deleteSource(source.id)}
                    >
                      <DeleteIcon fontSize="small" />
                    </ActionIconButton>
                  </SourceActions>
                </SourceItem>
              ))}
              
              {sources.length === 0 && (
                <EmptyState>
                  <p></p>
                </EmptyState>
              )}
            </SourcesList>
            
            <ButtonGroup>
              <Button className="secondary" onClick={closeSourcesModal}></Button>
            </ButtonGroup>
          </ModalContent>
        </Modal>
      )}
    </NewsContainer>
  );
};

export default News;