/**
 * URL
 * @param {string} url - URL
 * @returns {string} URL
 */
export const getFaviconUrl = (url) => {
  try {
    // URL
    const parsedUrl = new URL(url);
    // Google FaviconURL
    return `https://www.google.com/s2/favicons?domain=${parsedUrl.hostname}&sz=64`;
  } catch (error) {
    // URL
    console.error(':', error);
    return 'data:image/svg+xml;base64,';
  }
};

/**
 * URL
 * @param {string} url - URL
 * @returns {string} URL
 */
export const handleUrlChange = (url) => {
  if (!url) return '';
  
  // URL
  try {
    new URL(url);
    return url;
  } catch (e) {
    // URL
  }
  
  // 
  if (url.includes(':\\') || url.includes(':/')) {
    // Windows
    if (url.startsWith('"') && url.endsWith('"')) {
      url = url.slice(1, -1); // 
    }
    
    // 
    url = url.replace(/\\/g, '/');
    
    // file://
    if (!url.startsWith('file:/')) {
      url = `file:/${url}`;
    }
    
    return url;
  }
  
  // https://
  if (/^[a-zA-Z0-9][a-zA-Z0-9-]{1,61}[a-zA-Z0-9](?:\.[a-zA-Z]{2,})+/.test(url)) {
    return `https://${url}`;
  }
  
  return url;
};

/**
 * URL
 * @param {string} query - 
 * @returns {string} 
 */
export const cleanSearchQuery = (query) => {
  if (!query) return '';
  
  // 
  let cleaned = query.replace(/[\u4e00-\u9fa5]/g, '');
  
  // 
  cleaned = cleaned.replace(/\s+/g, '');
  
  // 
  const punctuationMap = {
    '': ',',
    '': '.',
    '': '!',
    '': '?',
    '': ':',
    '': ';',
    '\u201C': '"', // 
    '\u201D': '"', // 
    '\u2018': "'", // 
    '\u2019': "'", // 
    '': '(',
    '': ')',
    '': '[',
    '': ']',
    '': '<',
    '': '>',
    '': '-'
  };
  
  Object.keys(punctuationMap).forEach(key => {
    cleaned = cleaned.replace(new RegExp(key, 'g'), punctuationMap[key]);
  });
  
  // 
  cleaned = cleaned.replace(/\[.*?\]/g, '');
  
  return cleaned;
};

/**
 * URL
 * @param {string} str - 
 * @returns {boolean} URL
 */
export const isValidUrl = (str) => {
  try {
    new URL(str);
    return true;
  } catch (e) {
    return false;
  }
};

/**
 * URL
 * @param {string} query - 
 * @param {string} engine - bing, google, baidu, sogou, mita
 * @param {boolean} removeNonUrl - URL
 * @returns {string} URL
 */
export const buildSearchUrl = (query, engine = 'bing', removeNonUrl = false) => {
  if (!query) return '';
  
  // URL
  const processedQuery = removeNonUrl ? cleanSearchQuery(query) : query;
  
  // URL
  if (isValidUrl(processedQuery) || processedQuery.startsWith('file:/')) {
    return processedQuery;
  }
  
  // URL
  const encodedQuery = encodeURIComponent(processedQuery);
  
  switch (engine.toLowerCase()) {
    case 'google':
      return `https://www.google.com/search?q=${encodedQuery}`;
    case 'baidu':
      return `https://www.baidu.com/s?wd=${encodedQuery}`;
    case 'sogou':
      return `https://www.sogou.com/web?query=${encodedQuery}`;
    case 'mita':
      return `https://metaso.cn/#/search?q=${encodedQuery}`;
    case 'bing':
    default:
      return `https://www.bing.com/search?q=${encodedQuery}`;
  }
};

/**
 * URL
 * @param {string} url - URL
 * @returns {string} 
 */
export const extractDomain = (url) => {
  try {
    const parsedUrl = new URL(url);
    return parsedUrl.hostname;
  } catch (error) {
    console.error(':', error);
    return '';
  }
};

/**
 * URLfetch API
 * @param {string} url - URL
 * @returns {Promise<string>} 
 */
export const fetchPageTitle = async (url) => {
  try {
    const response = await fetch(url);
    const html = await response.text();
    const match = html.match(/<title>(.*?)<\/title>/i);
    return match ? match[1] : extractDomain(url);
  } catch (error) {
    console.error(':', error);
    return extractDomain(url);
  }
};